export function invalidatePresetCacheIfNeeded() {
  if (localStorage.getItem('presetVersion') !== PRESET_VERSION) {
    localStorage.clear();
    localStorage.setItem('presetVersion', PRESET_VERSION);
  }
}

export const PRESET_VERSION = '1';

